let hasGotEnvironment = false;
function getEnvironment()
{
    const isUsingGamedev = electron.app.commandLine.hasSwitch("gamedev");

    if (!hasGotEnvironment)
    {
        if (isUsingGamedev)
        {
            console.log("Using gamedev.");
        }

        hasGotEnvironment = true;
    }

    if (isUsingGamedev)
    {
        return gamedev;
    }

    return prod;
}

