const { ipcMain } = require("electron");

import
{
    getEnvironment,
} from "./environment";

import
{
    removeCredentialAsync,
    addCredentialsChangedHandlerAsync,
    addCredentialAsync,
    getCredentialsByAccountIdAsync,
    requestAuthTokenInfoForAuthCodeAsync,
    refreshAllCredentialsAsync,
    getCredentialsAsync,
    loadAllCredentialsAsync,
} from "./credentials";

import
{
    startServerAsync,
    endAllServersAsync,
} from "./server";

import
{
    BrowserWindow,
    session,
} from "electron";

ipcMain.handle("removeCredentialAsync", (event, credentialName) =>
{
    return removeCredentialAsync(credentialName);
});



function credentialsChangedHandler(credentials)
{
    BrowserWindow.getAllWindows()[0].webContents.send("credentialsChanged", credentials);
}

addCredentialsChangedHandlerAsync(credentialsChangedHandler);



function clearCookiesAsync()
{
    return session.defaultSession.cookies.get({})
    .then((cookies) =>
    {
        for (const cookie of cookies)
        {
            // Don't delete the epic device cookie which lets us skip 2FA
            if (cookie.name == "EPIC_DEVICE")
            {
                continue;
            }

            let url = "";
            url += cookie.secure ? "https://" : "http://";
            url += cookie.domain.charAt(0) === "." ? "www" : "";
            url += cookie.domain;
            url += cookie.path;

            session.defaultSession.cookies.remove(url, cookie.name);
        }
    });
}

ipcMain.handle("clearCookies", (event) =>
{
    return clearCookiesAsync();
});

ipcMain.handle("getCredentialsAsync", (event) =>
{
    return getCredentialsAsync();
});

ipcMain.handle("addCredentialAsync", (event, name, authTokenInfo) =>
{
    return addCredentialAsync(name, authTokenInfo);
});

ipcMain.handle("getCredentialsByAccountIdAsync", (event, accountId) =>
{
    return getCredentialsByAccountIdAsync(accountId);
});

ipcMain.handle("requestAuthTokenInfoForAuthCodeAsync", (event, authCode) =>
{
    return requestAuthTokenInfoForAuthCodeAsync(authCode);
});

ipcMain.handle("refreshAllCredentialsAsync", (event) =>
{
    return refreshAllCredentialsAsync();
});

ipcMain.handle("startServerAsync", (event, port) =>
{
    return startServerAsync(port);
});

ipcMain.handle("getEnvironmentAsync", (event) =>
{
    return Promise.resolve(getEnvironment());
});

ipcMain.handle("loadAllCredentialsAsync", (event) =>
{
    return Promise.resolve(loadAllCredentialsAsync());
});

ipcMain.handle("endAllServersAsync", (event) =>
{
    return Promise.resolve(endAllServersAsync());
});