import
{
    app,
    BrowserWindow,
    globalShortcut,
    shell
} from "electron";

import installExtension,
{
    REACT_DEVELOPER_TOOLS,
} from "electron-devtools-installer";

import
{
    saveAllCredentialsAsync,
} from "./credentials";

import "./ipc_main"

import
{
    endAllServersAsync,
} from "./server"

// Handle creating/removing shortcuts on Windows when installing/uninstalling.
if (require("electron-squirrel-startup"))
{
    app.quit();
}

const createWindow = () =>
{
    let window = new BrowserWindow(
    {
        width: 1350,
        height: 800,
        // Window should be resizable down to the left hand panel
        minWidth: 345,
        minHeight: 300,
        resizable: true,
        webPreferences:
        {
            webviewTag: true,
            preload: MAIN_WINDOW_PRELOAD_WEBPACK_ENTRY,
            nativeWindowOpen: false
        },
        show: false,
    });
    
    window.setMenu(null);

    window.loadURL(MAIN_WINDOW_WEBPACK_ENTRY);

    const isDevMode = !!process.execPath.match(/[\\/]electron/);

    // Only let dev tools be used in dev mode.
    // It's risky to let this be used in prod as users can be IP banned for using a chrome debugger on the account portal.
    if (isDevMode)
    {
        window.webContents.openDevTools();
        globalShortcut.register('CommandOrControl+Shift+I', () =>
        {
            if (window.isFocused())
            {
                window.webContents.toggleDevTools();
            }
        });
    }
    
    window.once("ready-to-show", () =>
    {
        window.show();
    });
};

// This method will be called when Electron has finished
// initialization and is ready to create browser windows.
// Some APIs can only be used after this event occurs.
app.whenReady()
.then(() =>
{
    installExtension(REACT_DEVELOPER_TOOLS);
    createWindow();
})
.catch((error) =>
{
    console.error(error);
});

// Various actions can trigger this event, such as launching the application for the first time,
// attempting to re-launch the application when it's already running, or clicking on the applications dock (macOS) or taskbar icon.
app.on("activate", () =>
{
    if (BrowserWindow.getAllWindows().length === 0)
    {
        createWindow();
    }
});

// On macOS it is common for applications and their menu bar to stay active until the user quits explicitly with CMD+Q.
app.on("window-all-closed", () =>
{
    endAllServersAsync()
    .then(saveAllCredentialsAsync)
    .then(() =>
    {
        globalShortcut.unregisterAll();

        if (process.platform !== "darwin")
        {
            app.quit();
        }
    })
});

app.on('web-contents-created', (event, contents) =>
{  
    contents.setWindowOpenHandler(({ url }) =>
    {
        if (url.startsWith("https://"))
        {
            shell.openExternal(url);
        }

        return { action: 'deny' }
    });    
});